import axios from "axios";
import { API_USER } from "../../utilities/constant";
import { handleApiResponse, handleApiError } from "../../utilities/handlerApi";

export const fetchUsers = async (auth) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.get(`${API_USER}/user-admin`, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "ユーザーの取得に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const createUsers = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/user-admin/create`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "ユーザーの作成に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const deleteUsers = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/user-admin/delete/${data.id}`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "ユーザーの削除に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};