import React, { useEffect, useState } from "react";
import { Box, TextField, Select, MenuItem, Button, FormHelperText, FormControl, InputLabel } from "@mui/material";
import { useAuth } from "../../context/AuthContext";
import { fetchUsers } from "../../actions/CallApi/UserFetch";
import { POSITION_OPTIONS } from "../../utilities/constant";


const ScheduleForm = ({
  formData,
  onChange,
  onSubmit,
  handleCancel,
  isSubmitting,
  submitLabel,
}) => {
  const [users, setUsers] = useState([]);
  const { callApiWithToken } = useAuth();
  const [errors, setErrors] = useState({});

  useEffect(() => {
    const fetchUsersData = async () => {
      try {
        const fetchedUsers = await callApiWithToken(fetchUsers);
        setUsers(fetchedUsers);
      } catch (err) {
        console.error("Error fetching users", err);
      }
    };
    fetchUsersData();
  }, [callApiWithToken]);

  const normalizeDateTime = (datetimeLocal) => {
    if (!datetimeLocal) return null;
    return datetimeLocal.length == 19? datetimeLocal :datetimeLocal + ":00";
  };

  const normalizeDate = (dateValue) => {
    if (!dateValue) return null;
    return dateValue;
  };

  const validate = () => {
    const newErrors = {};
    if (!formData.user_id) newErrors.user_id = "ユーザーは必須です";
    if (!formData.target_date) newErrors.target_date = "出勤日付は必須です";
    if (!formData.start_time) newErrors.start_time = "出勤時間は必須です";
    if (!formData.end_time) newErrors.end_time = "退勤時間は必須です";
    if (!formData.position) newErrors.position = "位置は必須です";
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = () => {
    if (!validate()) return;

    const payload = {
      user_id: formData.user_id,
      target_date: normalizeDate(formData.target_date),
      start_time: normalizeDateTime(formData.start_time),
      end_time: normalizeDateTime(formData.end_time),
      from_break_time: normalizeDateTime(formData.from_break_time),
      to_break_time: normalizeDateTime(formData.to_break_time),
      actual_start_time: normalizeDateTime(formData.actual_start_time),
      actual_end_time: normalizeDateTime(formData.actual_end_time),
      position: formData.position,
    };
    onSubmit(payload);
  };

  return (
    <Box component="form" noValidate autoComplete="off">
      <FormControl fullWidth margin="normal" error={!!errors.user_id}>
        <InputLabel id="user-select-label">名前</InputLabel>
        <Select
          labelId="user-select-label"
          name="user_id"
          value={formData.user_id || ""}
          onChange={(e) =>
            onChange({ target: { name: "user_id", value: e.target.value } })
          }
        >
          {users.map((u) => (
            <MenuItem key={u.id} value={u.id}>
              {u.name}
            </MenuItem>
          ))}
        </Select>
        {errors.user_id && <FormHelperText>{errors.user_id}</FormHelperText>}
      </FormControl>

      <TextField
        label="出勤日付"
        type="date"
        name="target_date"
        value={formData.target_date || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
        error={!!errors.target_date}
        helperText={errors.target_date}
      />

      <TextField
        label="出勤時間"
        type="datetime-local"
        name="start_time"
        value={formData.start_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
        error={!!errors.start_time}
        helperText={errors.start_time}
      />

      <TextField
        label="退勤時間"
        type="datetime-local"
        name="end_time"
        value={formData.end_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
        error={!!errors.end_time}
        helperText={errors.end_time}
      />

      <TextField
        label="休憩時間から"
        type="datetime-local"
        name="from_break_time"
        value={formData.from_break_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
      />

      <TextField
        label="休憩時間まで"
        type="datetime-local"
        name="to_break_time"
        value={formData.to_break_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
      />

      <TextField
        label="実際出勤"
        type="datetime-local"
        name="actual_start_time"
        value={formData.actual_start_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
      />

      <TextField
        label="実際退勤"
        type="datetime-local"
        name="actual_end_time"
        value={formData.actual_end_time || ""}
        onChange={onChange}
        fullWidth
        margin="normal"
        InputLabelProps={{ shrink: true }}
      />

      <FormControl fullWidth margin="normal" error={!!errors.position}>
        <InputLabel id="position-select-label">位置</InputLabel>
        <Select
          labelId="position-select-label"
          name="position"
          value={formData.position || ""}
          onChange={(e) =>
            onChange({ target: { name: "position", value: e.target.value } })
          }
        >
          <MenuItem value="">-- 位置選択 --</MenuItem>
          {POSITION_OPTIONS.map((pos) => (
            <MenuItem key={pos} value={pos}>
              {pos}
            </MenuItem>
          ))}
        </Select>
        {errors.position && <FormHelperText>{errors.position}</FormHelperText>}
      </FormControl>

      <Box mt={2} sx={{ display: "flex", gap: 1 }}>
        <Button
          variant="contained"
          color="primary"
          onClick={handleSubmit}
          disabled={isSubmitting}
        >
          {submitLabel}
        </Button>
        <Button variant="outlined" onClick={handleCancel}>
          キャンセル
        </Button>
      </Box>
    </Box>
  );
};

export default ScheduleForm;
