import React, { useState, useEffect } from "react";
import { Box, Typography, Paper } from "@mui/material";
import { useNavigate, useLocation } from "react-router-dom";
import ScheduleForm from "./ScheduleForm";
import { createSchedule, updateSchedule } from "../../actions/CallApi/ScheduleFetch";
import { useAuth } from "../../context/AuthContext";

const ScheduleAddOrEditPage = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { callApiWithToken } = useAuth();
  const initialData = location.state?.initialData;

  const [formData, setFormData] = useState({
    user_id: "",
    target_date: "",
    start_time: "",
    end_time: "",
    from_break_time: "",
    to_break_time: "",
    position: "",
    actual_start_time: "",
    actual_end_time: "",
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");

  useEffect(() => {
    if (initialData) {
      setFormData(initialData);
    }
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (payload) => {
    setIsSubmitting(true);
    setErrorMessage("");
    try {
      if (initialData?.id) {
        await callApiWithToken(updateSchedule, initialData.id, payload);
      } else {
        await callApiWithToken(createSchedule, payload);
      }

      navigate("/schedule");
    } catch (error) {
      setErrorMessage(error?.message || "API呼ぶときエーラー発生");
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleCancel = () => navigate("/schedule");

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        {initialData ? "✏️ 変更" : "➕ 追加"}
      </Typography>

      <Paper sx={{ p: 3 }}>
        <ScheduleForm
          formData={formData}
          onChange={handleChange}
          onSubmit={handleSubmit} // ✅ giờ sẽ nhận payload từ con
          handleCancel={handleCancel}
          isSubmitting={isSubmitting}
          submitLabel={initialData ? "変更" : "追加"}
        />
        {errorMessage && (
          <Typography color="error" mt={2}>
            {errorMessage}
          </Typography>
        )}
      </Paper>
    </Box>
  );
};

export default ScheduleAddOrEditPage;
