import axios from "axios";
import { API_USER } from "../../utilities/constant";
import { handleApiResponse, handleApiError } from "../../utilities/handlerApi";

export const fetchSchedule = async () => {
  try {
    const response = await axios.get(`${API_USER}/schedule`);
    return handleApiResponse(response, "メニューの取得に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const createSchedule = async (auth, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(`${API_USER}/schedule/create`, data, {
      headers: {
        Authorization: `${token_type} ${access_token}`,
      },
    });
    return handleApiResponse(response, "メニューの作成に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

export const updateSchedule = async (auth, menuId, data) => {
  const { access_token, token_type } = auth;
  try {
    const response = await axios.post(
      `${API_USER}/schedule/update/${menuId}`,
      data,
      {
        headers: {
          Authorization: `${token_type} ${access_token}`,
        },
      }
    );
    return handleApiResponse(response, "メニューの更新に失敗しました。");
  } catch (err) {
    handleApiError(err);
  }
};

